<template>
  <div>

  <el-card>

    <el-row>
      <el-col :sapn="4">
        <el-button size="small" type="primary" @click="addAccountVisible = true">添加帐号</el-button>
      </el-col>
    </el-row>

    <el-table
    :data="accountList"
    stripe
    v-loading="loading"
    style="width: 100%">
    <el-table-column
      type="index">
    </el-table-column>
    <el-table-column prop="apiKey" label="apiKey"></el-table-column>
    <el-table-column prop="desc" label="备注"></el-table-column>
    <el-table-column prop="platform" label="平台"></el-table-column>
    <el-table-column prop="coin" label="交易对"></el-table-column>
    <el-table-column prop="low" label="下沿"></el-table-column>
    <el-table-column prop="high" label="上沿"></el-table-column>
    <el-table-column prop="width" label="宽度"></el-table-column>
    <el-table-column prop="amount" label="开仓单元"></el-table-column>
    <el-table-column label="运行开关">
      <template slot-scope="scope">
        <el-switch
          v-model="scope.row.state"
          @change="stateChange(scope.row)"
          active-color="#13ce66"
          inactive-color="#ff4949"
          :active-value="1"
          :inactive-value="0">
        </el-switch>
      </template>
    </el-table-column>
    <el-table-column fixed="right" label="操作">
      <template slot-scope="scope">
        <el-button size="mini" type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope.row.id)"></el-button>
      </template>
    </el-table-column>
    </el-table>

  </el-card>

  <!-- 添加帐号对话框 -->
  <el-dialog
    title="添加帐号"
    :visible.sync="addAccountVisible"
    width="50%"
    @close="addAccountClose">
    <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="120px">
      <el-form-item label="apiKey" prop="apiKey">
        <el-input v-model="addForm.apiKey"></el-input>
      </el-form-item>
      <el-form-item label="secret" prop="secret">
        <el-input v-model="addForm.secret"></el-input>
      </el-form-item>
      <!-- <el-form-item label="passphrase">
        <el-input v-model="addForm.passphrase" placeholder="仅okex需要"></el-input>
      </el-form-item> -->
      <el-form-item label="交易对" prop="coin">
        <el-input v-model="addForm.coin"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="addForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="平台">
        <el-radio-group v-model="addForm.platform">
          <!-- <el-radio label="okex"></el-radio> -->
          <el-radio label="binance"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="下沿" prop="low">
        <el-input v-model="addForm.low"></el-input>
      </el-form-item>
      <el-form-item label="上沿" prop="high">
        <el-input v-model="addForm.high"></el-input>
      </el-form-item>
      <el-form-item label="宽度" prop="width">
        <el-input v-model="addForm.width"></el-input>
      </el-form-item>
      <el-form-item label="开仓单元" prop="amount">
        <el-input v-model="addForm.amount"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="addAccountVisible = false">取 消</el-button>
      <el-button type="primary" @click="addAccount">确 定</el-button>
    </span>
  </el-dialog>

  <!-- 编辑帐号对话框 -->
  <el-dialog
    title="编辑"
    :visible.sync="editDialogVisible"
    width="50%">
    <el-form ref="addFormRef" :model="editForm" :rules="addFormRules" label-width="120px">
      <el-form-item label="apiKey" prop="apiKey">
        <el-input v-model="editForm.apiKey"></el-input>
      </el-form-item>
      <el-form-item label="secret" prop="secret">
        <el-input v-model="editForm.secret"></el-input>
      </el-form-item>
      <!-- <el-form-item label="passphrase">
        <el-input v-model="addForm.passphrase" placeholder="仅okex需要"></el-input>
      </el-form-item> -->
      <el-form-item label="交易对" prop="coin">
        <el-input v-model="editForm.coin"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="editForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="平台">
        <el-radio-group v-model="editForm.platform">
          <!-- <el-radio label="okex"></el-radio> -->
          <el-radio label="binance"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="下沿" prop="low">
        <el-input v-model="editForm.low"></el-input>
      </el-form-item>
      <el-form-item label="上沿" prop="high">
        <el-input v-model="editForm.high"></el-input>
      </el-form-item>
      <el-form-item label="宽度" prop="width">
        <el-input v-model="editForm.width"></el-input>
      </el-form-item>
      <el-form-item label="开仓单元" prop="amount">
        <el-input v-model="editForm.amount"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button type="success" @click="getWangge">计算各网格范围</el-button>
      <el-button @click="editDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="editAccount">确 定</el-button>
    </span>
  </el-dialog>

  </div>

</template>

<script>
export default {
  data () {
    return {
      accountList: [],
      addAccountVisible: false,
      editDialogVisible: false,
      loading: true,
      addForm: {
        apiKey: '',
        secret: '',
        platform: 'binance',
        coin: '',
        desc: '',
        low: '',
        high: '',
        width: '',
        s: 'wangge'
      },
      editForm: {},
      addFormRules: { apiKey: [{ required: true, message: '请输入apiKey', trigger: 'blur' }], secret: [{ required: true, message: '请输入secret', trigger: 'blur' }], coin: [{ required: true, message: '请输入交易对', trigger: 'blur' }], low: [{ required: true, message: '请输入下沿', trigger: 'blur' }], high: [{ required: true, message: '请输入上沿', trigger: 'blur' }], width: [{ required: true, message: '请输入宽度', trigger: 'blur' }] }
    }
  },
  created () {
    this.getAccountList()
  },
  methods: {
    async getAccountList() {
      const { data: res } = await this.$http.get('accounts', { params: { s: 'wangge' } })
      if (!res || !res.success) return this.$message.error('获取帐号列表失败')
      this.loading = false
      this.accountList = res.data
    },
    async stateChange(row) {
      const { data: res } = await this.$http.post('account/changeState', { id: row.id, state: row.state, s: 'wangge' })
      if (!res || !res.success) {
        row.state = !row.state
        return this.$message.error('更新状态失败')
      }
    },
    addAccountClose() {
      this.$refs.addFormRef.resetFields()
    },
    addAccount() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('account/add', this.addForm)
        console.log(res)
        if (!res) {
          this.$message.error('添加帐号失败')
        }
        this.$message.success('添加帐号成功')
        this.addAccountVisible = false
        this.getAccountList()
      })
    },
    editAccount() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const edit = this.editForm
        edit.s = 'wangge'
        const { data: res } = await this.$http.post('account/edit', edit)
        console.log(res)
        if (!res) {
          this.$message.error('编辑帐号失败')
        }
        this.$message.success('编辑帐号成功')
        this.editDialogVisible = false
        this.getAccountList()
      })
    },
    getWangge() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const edit = this.editForm
        const { data: res } = await this.$http.post('account/edit/wangge/list', edit)
        if (!res) {
          this.$message.error('获取失败')
        }
        console.log(res)
        const list = res.data.list
        let sList = ''
        for (const item of list) {
          sList += item.low.toFixed(4) + ' - ' + item.high.toFixed(4) + '</br>'
        }
        const free = res.data.own.free
        const count = res.data.count
        console.log(edit)
        let unit = 0
        if (free > 0) {
          unit = free / ((edit.low + edit.high) / 2) / count
        }
        sList += '建议开仓单元: ' + unit + '</br>'
        sList += 'count: ' + count + '</br>'
        sList += 'USDT.free: ' + free + '</br>'
        this.$notify({
          title: '计算结果:',
          dangerouslyUseHTMLString: true,
          message: sList,
          duration: 0
        })
      })
    },
    async showEditDialog(id) {
      const { data: res } = await this.$http.post('account/query', { id, s: 'wangge' })
      console.log(res)
      if (!res) {
        this.$message.error('查询帐号失败')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    async open(row) {
      console.log(row)
      this.$notify({
        title: '计算结果:',
        dangerouslyUseHTMLString: true,
        message: '这是一条不会自动关闭的消息',
        duration: 0
      })
    }
  }
}
</script>

<style>
.text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
</style>
