<template>
  <div>
    <el-card>
      <div>
        <!-- 2 -->
        <div id="main1" style="width: 100%;height:400px;"></div>
        <div id="main2" style="width: 100%;height:400px;"></div>
        <div id="main3" style="width: 100%;height:400px;"></div>
        <div id="main4" style="width: 100%;height:400px;"></div>
        <div id="main5" style="width: 100%;height:400px;"></div>
      </div>
    </el-card>
  </div>
</template>

<script>

// 1
import echarts from 'echarts'

export default {
  data () {
    return {}
  },
  async mounted () {
    // 3. 基于准备好的dom，初始化echarts实例
    // var myChart1 = echarts.init(document.getElementById('main1'))

    // 4 .指定图表的配置项和数据
    const { data: res } = await this.$http.get('data/accountUsdt')
    if (res && res.success) {
      let count = 1
      const obj = res.data
      Object.keys(obj).forEach(function (key) {
        const myChart = echarts.init(document.getElementById('main' + count))
        const option = {
          title: {
            text: key
          },
          color: '#5470c6',
          tooltip: {},
          legend: {
            data: ['USDT']
          },
          xAxis: {
            type: 'category',
            data: obj[key].time
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            name: 'USDT',
            data: obj[key].usdt,
            smooth: true,
            type: 'line'
          }]
        }
        myChart.setOption(option)
        count++
      })
    }

    // option = {
    // xAxis: {
