<template>
  <div>

  <el-card>
    <el-table
    :data="accountList"
    v-loading="loading"
    style="width: 100%">
    <!-- <el-table-column
      type="index"> -->
    <!-- </el-table-column> -->
    <el-table-column label="ID">
      <template slot-scope="scope">
        {{ scope.row.conConfig.id }}
      </template>
    </el-table-column>
    <el-table-column label="平台" width="80px">
      <template slot-scope="scope">
        {{ scope.row.conConfig.platform }}
      </template>
    </el-table-column>
    <el-table-column label="交易对">
      <template slot-scope="scope">
        {{ scope.row.conConfig.coinPair }}
      </template>
    </el-table-column>
    <el-table-column label="可用持仓">
      <template slot-scope="scope">
        {{ scope.row.conConfig.coinPair.split('/')[0] }}:{{ scope.row.balance[scope.row.conConfig.coinPair.split('/')[0]] && scope.row.balance[scope.row.conConfig.coinPair.split('/')[0]].free || 0 }}
        <br>
        {{ scope.row.conConfig.coinPair.split('/')[1] }}:{{ scope.row.balance[scope.row.conConfig.coinPair.split('/')[1]] && scope.row.balance[scope.row.conConfig.coinPair.split('/')[1]].free || 0 }}
      </template>
    </el-table-column>
    <el-table-column label="通道">
      <template slot-scope="scope">
        上沿:{{ scope.row.algo.don_open }}<br>
        下沿:{{ scope.row.algo.don_close }}<br>
        ATR:{{ scope.row.algo.atr }}
      </template>
    </el-table-column>
    <el-table-column prop="lastClosePrice" label="最新成交价"></el-table-column>
    <el-table-column prop="unit" label="建仓单元"></el-table-column>
    <el-table-column label="最小开单量">
      <template slot-scope="scope">
        {{ scope.row.symbolLimit.amount.min }}
      </template>
    </el-table-column>
    <el-table-column label="建仓预消耗本金">
      <template slot-scope="scope">
        {{ scope.row.unit * scope.row.open_point }}
      </template>
    </el-table-column>
    <el-table-column label="是否持仓">
      <template slot-scope="scope">
        {{ scope.row.isHoldPosition }}
      </template>
    </el-table-column>
    <el-table-column label="上次持仓/加仓价">
      <template slot-scope="scope">
        {{ scope.row.lastBuyPrice }}
      </template>
    </el-table-column>
    <el-table-column prop="winPoint" label="止盈点"></el-table-column>
    <el-table-column prop="stopLossPoint" label="止损点"></el-table-column>
    <el-table-column prop="addPoint" label="加仓点"></el-table-column>
    <!-- <el-table-column label="conConfig">
      <template slot-scope="scope">
        {{ scope.row.conConfig }}
      </template>
    </el-table-column> -->
    </el-table>
    <!-- 分页 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="queryInfo.pagenum"
      :page-size="queryInfo.pagesize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </el-card>
  </div>

</template>

<script>
export default {
  data () {
    return {
      accountList: [],
      loading: true,
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0
    }
  },
  created () {
    this.getAccountList()
  },
  methods: {
    async getAccountList() {
      const { data: res } = await this.$http.get('data/strategy', { params: { pagenum: this.queryInfo.pagenum } })
      if (!res || !res.success) {
        const { data: res } = await this.$http.get('data/strategy', { params: {} })
        if (!res || !res.success) {
          return this.$message.error('获取列表失败')
        }
      }
      this.loading = false
      this.accountList = res.data
      this.total = res.total
    },
    handleSizeChange(newSize) {
      console.log(newSize)
    },
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.loading = true
      this.getAccountList()
    }
  }
}
</script>

<style>
.text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
</style>
